<template>
  <div class="col-md-4">
    <div class="card mb-4 shadow-sm">
      <div class="d-flex">
        <button class="btn btn-block">好</button>
        <button class="btn btn-block">不好</button>
      </div>
      <img :src="imgPath" width="100%">
    </div>
  </div>
</template>

<script>
export default {
  name: 'MyImg',
  props: ['path'],
  computed: {
    imgPath: function () {
      try {
        if (this.path.length === 0) {
          return ''
        }
        let imgPath = this.path.replace(/&/g, '%26')
        let url = 'http://localhost:8087/img?path=' + this.$store.state.rootPath + imgPath
        return url
      } catch (e) {
        console.log(e)
      }
    }
  }
}
</script>

<style scoped>
  .img_view {
    cursor: pointer;
  }
</style>



