<template>
  <div>
    <navbar/>
    <div class="jumbotron text-center">
      <div class="row">
        <RawImg :rawPath="raw"></RawImg>
        <div class="row col-md-9" style="padding-left: 70px">
          <MyImg
            v-for="image in results"
            :key="image.path"
            :path="image.path"
          ></MyImg>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import notateAPI from '@/api/NotationAPI'

import navbar from '@/components/Navbar'
import RawImg from '@/components/RawImg'
import MyImg from '@/components/MyImg'
export default {
  name: 'retargeting',
  components: {
    navbar,
    RawImg,
    MyImg
  },
  data () {
    return {
      total: 0,
      sample: {
        images: []
      }
    }
  },
  methods: {
    nextTask () {
      if (!this.check()) {
        return
      }
      let sequenceNum = this.sample.sequenceNum
      this.getTask(sequenceNum + 1)
    },
    previousTask () {
      if (!this.check()) {
        return
      }
      let sequenceNum = this.sample.sequenceNum
      if (sequenceNum <= 1) {
        return
      }
      this.getTask(sequenceNum - 1)
    },
    check () {

    },
    getTask (sequenceNum) {

    },
    loadAll () {

    },
    submmit () {

    }
  },
  computed: {
    raw: function () {
      let raw = this.sample.images[0]
      return raw ? raw.path : ''
    },
    results: function () {
      return this.sample.images.slice(1)
    }
  },
  created () {
    let username = this.$store.state.username
    let notateType = this.$store.state.notateType
    notateAPI.getLatestWork(username, notateType).then(res => {
      this.sample = res.data.sample
      this.total = res.data.count
    }).catch(e => {
      console.log('error ' + JSON.stringify(e))
    })
  }
}
</script>

<style scoped>

</style>



